/* purgecss start ignore */

#home-background{
  background-repeat: no-repeat;
  background-size: 100% auto;
  width: 100%;
  height: 50px;
  padding-top: 50%;
  margin-top: -30px;
  -webkit-filter: brightness(30%);
  filter: brightness(30%);
}

#introduction{
  position: absolute;
  top: 150px;
  left: 0;
  width: 100%;
  color: white;
}

#introduction div{
  margin-top: calc(20vw);
}

#steps .absolute{
  right: 0;
}

#steps .absolute:first-child{
  margin-top: -500px;
  right: 30px;
}

#steps .absolute:nth-child(2){
  margin-top: -350px;
  right: 60px;
}

#steps .absolute:last-child{
  margin-top: -200px;
  right: 120px;
}

@media(min-width: 992px){
  #introduction div{
    margin-top: calc(32vw);
  }
}

@media(max-width: 600px){
  #home-background{
    margin-top: -31px;
  }

  #introduction{
    margin-top: -20px;
  }

  #introduction div{
    margin-top: calc(7vw);
  }

  #steps .absolute{
    width: 90vw;
    right: 0 !important;
    left: 0 !important;
    margin-left: auto;
    margin-right: auto;
  }

  #steps .absolute:first-child{
    margin-top: -450px;
  }

  #steps .absolute:nth-child(2){
    margin-top: -300px;
  }

  #steps .absolute:last-child{
    margin-top: -150px;
  }
}

h1, h2, h3, h4, h5, h6{
  font-family: 'Fredoka One', cursive;
}

